import React,{useState, useEffect} from "react"
import Axios from "axios"
import "./ItemType.css"
import CategoryIcon from '@mui/icons-material/Category'
import CancelIcon from '@mui/icons-material/Cancel'
import {Modal, Alert, AlertTitle, Paper, Divider, Button, TextField, Typography, Badge} from '@mui/material'
import { DataGrid, GridToolbar } from '@mui/x-data-grid'
import AddCircleIcon from '@mui/icons-material/AddCircle'
import DeleteForeverIcon from '@mui/icons-material/DeleteForever'

export default function DeviceLocations(props){
    const [locationNote , setLocationNote] = useState(true)
    const [locationData , setLocationData] = useState([{
        stdev_id: "",
        stdev_location_name:""
    }])
    const [addLocationName, setAddLocationName] = useState({
        location_name:""
    })
    const [addStatus , setAddStatus] = useState({
        add: "",
        notdeleted: [],
        deleted: []
    })
    const [selectedLocationId , setSelectedLocationId] = useState([])
    const [openModal, setOpenModal] = useState(false)
    const [ NamestoDelete , setNamestoDelete ] = useState([])

    useEffect(()=>{
        Axios.get(props.testingIn + "/location")
        .then((response) => {
            setLocationData(response.data)
        });
    },[])
    
    const columns = [
        { field: 'stdev_location_name', headerName: 'LOCATION', width: 300 },
        { field: 'count', headerName: 'No. OF ASSIGNED DEVICE(S)', width: 230 }
    ]

    const handleSubmitLocationName =() =>{
        Axios.post(props.testingIn + "/addLocations", addLocationName)
        .then((response) => {
            setAddStatus({
                add: response.data.message,
                notdeleted: [],
                deleted: []
            })
            setLocationData(response.data.Result)
            setAddLocationName({location_name: ""})
        });
    }

    const deleteSelectedNames = () => {
        if(selectedLocationId.length > 0 ){
            Axios.post(props.testingIn + "/selectedLocationName", selectedLocationId)
            .then((response) => {
                setAddStatus({
                    add: '',
                    notdeleted: response.data.notDelete,
                    deleted: response.data.fordeleteIds
                })
            });
        }else{
            setAddStatus({
                add: '',
                notdeleted: [],
                deleted: []
            })
        }
    }

    const sureDelete = () =>{
        Axios.post(props.testingIn + "/deleteLocationName",selectedLocationId)
        .then((response) => {
            setAddStatus({
                add: response.data.message,
                notdeleted: [],
                deleted: []
            })
            setLocationData(response.data.Result)
            setOpenModal(false)
        })
    }

    useEffect(()=>{
        if(addStatus.notdeleted.length === 0 && addStatus.deleted.length > 0){
            const name = []
            selectedLocationId.map( ids => { 
                locationData.map( location => { if(ids === location.stdev_id)  name.push(location.stdev_location_name)  })
            })
            console.log(name)
            setNamestoDelete(name)
            setOpenModal(true)
        }
    },[addStatus])

    return(
        <div className="ItemType">
            <Modal
                open={openModal}
                onClose={() => {setOpenModal(false)}}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Alert 
                    severity="error" 
                    sx={{
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        transform: 'translate(-50%, -50%)',
                        width: 400,
                        boxShadow: 24,
                        p: 4,
                    }}
                >
                    <AlertTitle><strong>Warning !</strong> </AlertTitle>
                    <Typography sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        gap: '10px'
                    }}>
                        <span>
                            Are you sure you want to Delete ( {NamestoDelete.toString()} ) ?
                        </span>
                        <div>
                            <Button 
                                variant="contained" 
                                color="error" 
                                sx={{
                                    width: '40%',
                                    marginRight: '1vw'
                                }}
                                onClick={sureDelete}
                            >
                                <DeleteForeverIcon />
                                Delete
                            </Button>
                            <Button 
                                variant="contained" 
                                color="error" 
                                sx={{
                                    width: '40%'
                                }}
                                onClick={()=> setOpenModal(false) }
                            >
                                <CancelIcon />
                                Cancel
                            </Button>
                        </div>
                        
                    </Typography>
                </Alert>
            </Modal>
            
            <Paper elevation={12} className="ItemType--addDevice">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemType--header">
                    <span><AddCircleIcon fontSize="small" /> Add Location Name</span>
                </Typography>
                <Divider />
                <span className="ItemType--addform">
                    <TextField 
                        id="standard-basic" 
                        label="Device Name" 
                        variant="outlined" 
                        sx={{
                            width: '90%',
                            marginLeft: '20px'
                        }}
                        value={addLocationName.location_name}
                        onChange={(event)=>{
                            setAddLocationName({location_name: event.target.value})
                        }}
                    />
                    <Button 
                        variant="contained" 
                        color="info" 
                        sx={{
                            width: '30%',
                            marginLeft: '20px'
                        }}
                        onClick={handleSubmitLocationName}
                    >
                        <AddCircleIcon />
                        Save
                    </Button>
                </span>
            </Paper>
            <div className="ItemType--half">
                {locationNote && 
                    <Alert severity="warning" className="ItemType--welcome" onClose={()=> setLocationNote(prevValue => !prevValue)}>
                        <strong>Note! : </strong>
                        Select the checbox if you want to delete
                    </Alert>
                }
                <Paper className="ItemType--contents" elevation={12}>
                    <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemType--header">
                        <span><CategoryIcon fontSize="small" /> Item Type(s) List</span>
                        <span style={{marginRight: '20px'}}>
                            Number of All Locations:<Badge badgeContent={locationData.length} style={{marginLeft: '15px'}} color="primary" />
                        </span>
                    </Typography>
                    <Divider />
                    <div className="ItemType--body">
                        <div className="ItemType--delete">
                            <span style={{
                                display: 'flex',
                                alignItems: 'end'
                            }}>
                                <Button variant="contained"  size="large" color="error" onClick={deleteSelectedNames}>
                                    Delete
                                </Button>
                            </span>
                            {addStatus.add && 
                                <Alert severity="success" sx={{width: '77%'}}>
                                    <AlertTitle><strong>Success</strong> </AlertTitle>
                                    {addStatus.add}
                                </Alert>
                            }
                            {addStatus.notdeleted.length > 0 && 
                                <Alert severity="error" sx={{width: '77%'}}>
                                    <AlertTitle><strong>Cannot Delete Location(s): ( { addStatus.notdeleted.toString()} )</strong> </AlertTitle>
                                    Possible Reasons: Still possesses an Item.
                                </Alert>
                            }
                        </div>
                        <div className="ItemType--table">
                            <DataGrid
                                getRowId={(locationData) => locationData.stdev_id}
                                rows={locationData}
                                columns={columns}
                                pageSize={8}
                                rowsPerPageOptions={[8]}
                                checkboxSelection
                                disableColumnFilter
                                disableColumnSelector
                                disableDensitySelector
                                components={{ Toolbar: GridToolbar }}
                                componentsProps={{
                                    toolbar: {
                                        showQuickFilter: true,
                                        quickFilterProps: { debounceMs: 500 },
                                    },
                                }}   
                                onSelectionModelChange={(newSelectionModel) => {
                                    setSelectedLocationId(newSelectionModel)
                                }} 
                                onCellDoubleClick={(params, event) => {
                                    console.log(params.row)
                                    props.locationDetails(params.row)
                                    props.handleScreen("")
                                }}
                            />
                        </div>
                    </div>
                </Paper>
            </div>
        </div>
    )
}