import React, { useEffect, useState } from "react";
import Axios from "axios";
import './App.css';
import Login from "./Login"
import Main from "./Main"
import {Paper, Backdrop } from '@mui/material/'
import CircularProgress from '@mui/material/CircularProgress'
import { ThemeProvider, createTheme } from '@mui/material/styles'


function App() {
  const forOnline = "https://ams-server-svu7.onrender.com"
  const forOffline = "http://localhost:3001"
  const DecidedUsed = forOnline

  const [ loading , setLoading ] = useState( true )

  const [loggedInUser, setLoggedInUser] = useState([
    {
      username : "",
      fname : "",
      lname : ""
    }
  ])

  const [loginStatus, setLoginStatus] = useState(null)

  const [ useTheme, setUseTheme ] = useState(true)

  Axios.defaults.withCredentials = true



  useEffect(() => {
    Axios.get( DecidedUsed + "/login")
    .then((response) => {
      if (response.data.loggedIn === true) {
        setLoginStatus(response.data.loggedIn)
        setLoggedInUser(response.data.user[0])
        
      }
      setLoading(false)
    });
  }, [])

  const approveLogIn = (response) =>{
    setLoginStatus(true)
    
    setLoggedInUser(response.data.result[0])
  }

  const logout = () =>{
    Axios.post( DecidedUsed + "/logout")
    .then((response) => {
      setLoginStatus(false)
    })
  }

  const darkTheme = createTheme({
    palette: {
      mode: 'dark',
      primary: {
        main: '#3f51b5',
      },
      secondary: {
        main: '#f50057',
      },
      divider: 'rgb(26,129,207)',
    },
  })

  const lightTheme = createTheme({
    palette: {
      mode: 'light',
      primary: {
        main: '#3f51b5',
      },
      secondary: {
        main: '#f50057',
      },
    },
  })

  const changeTheme = (event) => {
    const check = event.target.checked
    check ? setUseTheme(true) : setUseTheme(false)
  }

  return (
    <>{ loading ? 
        <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={loading}>
          <CircularProgress color="inherit" />
        </Backdrop>
        : ""
      }
      {useTheme ? 
        <ThemeProvider theme={darkTheme}>
          <Paper className="App" color="primary" elevation={10}>
            {
              loginStatus ?
                <Main 
                  currentUser = {loggedInUser} 
                  handleLogout = {logout}
                  testingIn = {DecidedUsed}
                  handleTheme = {changeTheme}
                  currentTheme = {useTheme}
                />
              :
                  <Login 
                    handleLogin={approveLogIn} 
                    testingIn = {DecidedUsed}
                  /> 
            }
          </Paper>
        </ThemeProvider> :
        <ThemeProvider theme={lightTheme}>
        <Paper className="App" color="primary" elevation={10}>
          {
            loginStatus ?
              <Main 
                currentUser = {loggedInUser} 
                handleLogout = {logout}
                testingIn = {DecidedUsed}
                handleTheme = {changeTheme}
              />
            :
              <Login 
                handleLogin={approveLogIn} 
                testingIn = {DecidedUsed}
              /> 
          }
        </Paper>
      </ThemeProvider>
      }
    </>
  )
}

export default App;
