import React,{useState} from "react"
import "./ContentPanel.css"
import Dashboard from "./Dashboard"
import ItemStocks from "./ItemStocks"
import ItemType from "./ItemType"
import AssignItems from "./AssignItems"
import DeviceLocations from "./DeviceLocations"
import SystemUser from "./SystemUser"
import ActivityLog from "./ActivityLog"
import UserLog from "./UserLog"
import AddDevice from "./AddDevice"
import LocationDetails from "./LocationDetails"

export default function ContentPanel(props){
    const [currentLocationDetails, setCurrentLocationDetails] = useState("")
    const locationDetails = (location) => {
        setCurrentLocationDetails( location )
    }
    return(
        <div className="ContentPanel">
            {props.currentScreen === "Dashboard" && <Dashboard currentUser={props.currentUser} testingIn = {props.testingIn} />}
            {props.currentScreen === "ItemStocks" && <ItemStocks currentUser={props.currentUser} testingIn = {props.testingIn} />}
            {props.currentScreen === "ItemType" && <ItemType currentUser={props.currentUser} testingIn = {props.testingIn} />}
            {props.currentScreen === "AssignItems" && <AssignItems locationDetails = {locationDetails} currentUser={props.currentUser} handleScreen={props.handleScreen} testingIn = {props.testingIn}/>}
            {props.currentScreen === "DeviceLocations" && <DeviceLocations locationDetails = {locationDetails} handleScreen = {props.handleScreen} currentUser={props.currentUser} testingIn = {props.testingIn} />}
            {props.currentScreen === "SystemUser" && props.currentUser.username !== "Viewer" && <SystemUser currentUser={props.currentUser} testingIn = {props.testingIn} />}
            {props.currentScreen === "ActivityLog" && <ActivityLog currentUser={props.currentUser} testingIn = {props.testingIn} />}
            {props.currentScreen === "UserLog" && <UserLog currentUser={props.currentUser} testingIn = {props.testingIn} />}
            {props.currentScreen === "AddDevice" && props.currentUser.username !== "Viewer" &&  <AddDevice currentUser={props.currentUser} handleScreen={props.handleScreen} testingIn = {props.testingIn} />}
            {props.currentScreen === "" && <LocationDetails currentUser={props.currentUser} location={currentLocationDetails} testingIn = {props.testingIn} handleScreen = {props.handleScreen} />}
        </div>
    )
}