import React,{useState, useEffect} from "react"
import Axios from "axios"
import "./ItemStocks.css"
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import CategoryIcon from '@mui/icons-material/Category'
import {Alert, Paper, Divider, Button, TextField, Typography, Badge} from '@mui/material'
import { DataGrid, GridToolbar} from '@mui/x-data-grid'

export default function LocationDetails(props){
    const [locationDetailsData, setLocationDetailsData] = useState([])
    const [locationDetailsNote, setLocationDetailsNote] = useState(true)
    const [selectedItem, setSelectedItem] = useState([])
    console.log(selectedItem)
    // {props.location.stdev_location_name + " " + props.location.stdev_id}
    useEffect(()=>{
        console.log(props.location.stdev_id)
        Axios.post(props.testingIn + "/locationDetails", props.location)
        .then((response) => {
            setLocationDetailsData(response.data)
        })
    },[])


    const columns = [
        { field: 'date_deployment', headerName: "DATE TRANSFERED", width: 200 },
        { field: 'dev_name', headerName: 'ITEM TYPE', width: 130 },
        { field: 'dev_desc', headerName: "ITEMS DESCRIPTION", width: 350 },
        { field: 'dev_serial', headerName: "INVENTORY CODE", width: 250 },
        { field: 'dev_brand', headerName: "ITEM'S BRAND", width: 170 },
        { field: 'dev_model', headerName: "MODEL", width: 170 },
        { field: 'dev_status', headerName: "STATUS", width: 130 },
    ]

    const handleReturnButton = () => {
        Axios.post(props.testingIn + "/returnItems", selectedItem)
        .then((response) => {
            // setLocationDetailsData(response.data)
            props.handleScreen("AssignItems")
        })
    }

    return(
        <div className="ItemStocks">
            {locationDetailsNote && 
                <>
                    {props.currentUser.username === "Viewer" ? 
                        <Alert severity="info" className="ItemStocks--welcome" onClose={()=> setLocationDetailsNote(prevValue => !prevValue)}>
                            <strong>Note! : </strong>
                            In Viewer's mode most of the functionalities are disabled.
                        </Alert>
                    :
                        <Alert severity="info" className="ItemStocks--welcome" onClose={()=> setLocationDetailsNote(prevValue => !prevValue)}>
                            <strong>Note! : </strong>
                            Select the checbox if you want to issue return item(s) before clicking "return" button.
                        </Alert>
                    }
                </>
            }
            <Paper elevation={12} className="ItemStocks--contents">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemStocks--header">
                    <Button variant="contained" color="info" onClick={()=> props.handleScreen("DeviceLocations")}><ArrowBackIosIcon />Back</Button>
                    <span style={{marginRight: '20px'}}>
                        {props.location.stdev_location_name}'s Number of All Device:<Badge badgeContent={locationDetailsData.length} style={{marginLeft: '15px'}} color="primary" />
                    </span>
                </Typography>
                <Divider />
                <div className="ItemStocks--body">
                    {  props.currentUser.username === "Viewer" ? 
                        <>
                            <div className="Box--search">
                                <Button variant="contained" color="info">Return</Button>
                            </div>
                            <div className="ItemStock--table">
                                <DataGrid
                                    getRowId={(locationDetailsData) => locationDetailsData.Id_id}
                                    rows={locationDetailsData}
                                    columns={columns}
                                    pageSize={9}
                                    rowsPerPageOptions={[9]}
                                />
                            </div> 
                        </>
                        : 
                        <>
                            <div className="Box--search">
                                <Button variant="contained" color="info" onClick={handleReturnButton}>Return</Button>
                            </div>
                            <div className="ItemStock--table">
                                <DataGrid
                                    getRowId={(locationDetailsData) => locationDetailsData.Id_id}
                                    rows={locationDetailsData}
                                    columns={columns}
                                    pageSize={9}
                                    rowsPerPageOptions={[9]}
                                    checkboxSelection
                                    disableColumnFilter
                                    disableColumnSelector
                                    disableDensitySelector
                                    components={{ Toolbar: GridToolbar }}
                                    componentsProps={{
                                        toolbar: {
                                            showQuickFilter: true,
                                            quickFilterProps: { debounceMs: 500 },
                                        },
                                    }} 
                                    onSelectionModelChange={(newSelectionModel) => {
                                        setSelectedItem(newSelectionModel)
                                    }}
                                />
                            </div>
                        </>
                    }
                    
                    
                </div>
            </Paper>
        </div>
    )
}