import React,{useState, useEffect} from "react"
import Axios from "axios"
import "./ItemStocks.css"
import CategoryIcon from '@mui/icons-material/Category';
import {Alert, Paper, Divider, Button, TextField, Typography, Badge} from '@mui/material'
import { DataGrid, GridToolbar} from '@mui/x-data-grid'

export default function ItemStocks(props){
    const [itemStockNote , setItemStockNote] = useState(true)
    const [itemStockData , setItemStockData] = useState([])

    const columns = [
        { field: 'dev_status', headerName: "STATUS", width: 130 },
        { field: 'dev_name', headerName: 'ITEM TYPE', width: 130 },
        { field: 'dev_desc', headerName: "ITEMS DESCRIPTION", width: 450 },
        { field: 'dev_serial', headerName: "INVENTORY CODE", width: 250 },
        { field: 'dev_brand', headerName: "ITEM'S BRAND", width: 170 },
        { field: 'dev_model', headerName: "MODEL", width: 170 },
        { field: 'action', headerName: "ACTION", width: 130 },
    ]

    useEffect(()=>{
        Axios.get(props.testingIn + "/stdevices")
        .then((response) => {
            setItemStockData(response.data)
        });
    },[])

    return(
        <div className="ItemStocks">
            {itemStockNote && 
                <>
                    {props.currentUser.username === "Viewer" ? 
                        <Alert severity="info" className="ItemStocks--welcome" onClose={()=> setItemStockNote(prevValue => !prevValue)}>
                            <strong>Note! : </strong>
                            In Viewer's mode most of the functionalities are disabled.
                        </Alert>
                    :
                        <Alert severity="info" className="ItemStocks--welcome" onClose={()=> setItemStockNote(prevValue => !prevValue)}>
                            <strong>Note! : </strong>
                            Select the checbox if you want to delete. You can only check unused items.
                        </Alert>
                    }
                </>
            }
            <Paper elevation={12} className="ItemStocks--contents">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemStocks--header">
                    <span><CategoryIcon fontSize="small" /> Item Type(s) List</span>
                    <span style={{marginRight: '20px'}}>
                        Number of All Device:<Badge badgeContent={itemStockData.length} style={{marginLeft: '15px'}} color="primary" />
                    </span>
                </Typography>
                <Divider />
                <div className="ItemStocks--body">
                    {  props.currentUser.username === "Viewer" ? 
                        <>
                            <div className="Box--search">
                                <Button variant="contained" color="error">Mark as Deadpool</Button>
                            </div>
                            <div className="ItemStock--table">
                                <DataGrid
                                    getRowId={(itemStockData) => itemStockData.id}
                                    rows={itemStockData}
                                    columns={columns}
                                    pageSize={9}
                                    rowsPerPageOptions={[9]}
                                />
                            </div> 
                        </>
                        : 
                        <>
                            <div className="Box--search">
                                <Button variant="contained" color="error">Mark as Deadpool</Button>
                            </div>
                            <div className="ItemStock--table">
                                <DataGrid
                                    getRowId={(itemStockData) => itemStockData.id}
                                    rows={itemStockData}
                                    columns={columns}
                                    pageSize={9}
                                    rowsPerPageOptions={[9]}
                                    checkboxSelection
                                    disableColumnFilter
                                    disableColumnSelector
                                    disableDensitySelector
                                    components={{ Toolbar: GridToolbar }}
                                    componentsProps={{
                                        toolbar: {
                                            showQuickFilter: true,
                                            quickFilterProps: { debounceMs: 500 },
                                        },
                                    }} 
                                />
                            </div>
                        </>
                    }
                    
                    
                </div>
            </Paper>
        </div>
    )
}