import React,{useState} from "react"
import "./NavBar.css"
import MenuIcon from '@mui/icons-material/Menu'
import NightlightRoundIcon from '@mui/icons-material/NightlightRound'
import LightModeIcon from '@mui/icons-material/LightMode'
import {AppBar, Toolbar, Typography, IconButton, Menu, MenuItem, Checkbox } from '@mui/material'


export default function NavBar(props){
    const [anchorEl, setAnchorEl] = useState(null);

    const openthisMenu = (event) =>{
        setAnchorEl(event.currentTarget)
    }

    return (
        <AppBar position="static" enableColorOnDark>
            <Toolbar>
                <Typography variant="h6" component="div" sx={{ flexGrow: 1 , textAlign:'left'}}>
                    A.M.S. Admin Panel
                </Typography>
                <div>
                    <Checkbox
                        onChange={props.handleTheme}
                        checked={props.currentTheme}
                        icon={<LightModeIcon sx={{color: 'orange', transitionTimingFunction: 'ease-in-out'}} />}
                        checkedIcon={<NightlightRoundIcon sx={{color: 'white', transitionTimingFunction: 'ease-in-out'}} />}
                        
                    />
                    <IconButton
                        size="large"
                        aria-label="account of current user"
                        aria-controls="menu-appbar"
                        aria-haspopup="true"
                        onClick={openthisMenu}
                        color="inherit"
                    >
                        <MenuIcon />
                    </IconButton>
                    <Menu
                        id="menu-appbar"
                        anchorEl={anchorEl}
                        anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        keepMounted
                        transformOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        open={Boolean(anchorEl)}
                        onClose={()=>{ setAnchorEl(null) }}
                    >
                        <MenuItem >Change Password</MenuItem>
                        <MenuItem onClick={props.logOut}>Log out</MenuItem>
                    </Menu>
                </div>
            
            </Toolbar>
        </AppBar>
    )
}